import * as phina from "phina.js";

phina.globalize();

// DisplayElementalpha
export default phina.define('MyDisplayElement', {
  superClass: 'phina.display.DisplayElement',
  init: function() {
    this.superInit();
  },
  // 
  // alpha: (0~1)
  // duration: ()
  setAlpha: function(alpha, duration = 0) {
    this.children.forEach(object => {
      if (object.children.length) {
        if ('setAlpha' in object) {
          object.setAlpha(alpha, duration);
        }
      } else if (duration) {
        object.tweener.to({alpha: alpha}, duration).play();
      } else {
        object.alpha = alpha;
      }
    });
    return this;
  },
  // 
  hide: function() {
    this.children.forEach(object => {
      object.hide();
    });
    return this;
  },
  // 
  fadeOut: function(duration) {
    this.children.forEach(object => {
      object.tweener.fadeOut(duration).play();
    });
    return this;
  },
  // 
  fadeIn: function(duration) {
    this.children.forEach(object => {
      object.tweener.fadeIn(duration).play();
    });
    return this;
  },
});