import * as phina from "phina.js"
import ImmovablePosition from "../display/immovablePosition"
import StartPosition from "../display/startPosition/startPosition"

phina.globalize();

// 
// 
// boardData()
//   board, boardSize, startPosition, immovablePositions
// screenData()
//   screenPixelHeight, screenPixelWidth, baseDropSize, dropScale, topMargin, leftMargin, rightMargin
export default phina.define('BaseScene', {
  superClass: 'DisplayScene',
  init: function(options) {
    this.superInit({
      width: options.width,
      height: options.height,
    });

    // 
    this._setAttributes(options);
    this._setComputedAttributes();

    // 
    const imgName = `tile_${this.boardSize}`;
    phina.display.Sprite(imgName).setOrigin(0, 0).addChildTo(this);

    const DisplayElement = phina.display.DisplayElement;
    // 
    this.buttonGroup = DisplayElement().moveTo(0, this.topMargin - 136).addChildTo(this);
    // 
    this.dropGroup = DisplayElement().moveTo(this.leftMargin, this.topMargin).addChildTo(this);
    // 
    this.harassmentGroup = DisplayElement().moveTo(this.leftMargin, this.topMargin).addChildTo(this);
  },

  // 
  _setAttributes: function(options) {
    const { boardData, screenData } = options;

    // Vue
    this.vueComponent = options.vueComponent;

    // 
    this.boardData = boardData;
    this.screenData = screenData;

    // (this)
    Object.assign(this, boardData, screenData);

    // (maininput)
    this.board = Array.from(this.board);
  },

  // 
  _setComputedAttributes: function() {
    // 
    this.boardHeight = Number(this.boardSize.split('x')[0]);
    this.boardWidth = Number(this.boardSize.split('x')[1]);
    // 
    this.boardPixelHeight = this.screenPixelHeight - this.topMargin;
    this.boardPixelWidth = this.screenPixelWidth - (this.leftMargin + this.rightMargin);
    // 
    this.dropSize = this.boardPixelWidth / this.boardWidth;
    // 
    this.dropGridX = phina.util.Grid(this.boardPixelWidth, this.boardWidth),
    this.dropGridY = phina.util.Grid(this.boardPixelHeight, this.boardHeight);    
  },

  // 
  get1dIndex: function(X, Y) {
    return this.boardWidth * Y + X;
  },

  // 
  get2dIndex: function(index) {
    const X = index % this.boardWidth,
          Y = Math.floor(index / this.boardWidth);
    return [X, Y];
  },

  // (pixel)
  // 2
  getCoordinatesOfDrop: function(X, Y) {
    if (typeof(X) === 'number') {
      if (typeof(Y) !== 'number') {
        const index = X;
        X = index % this.boardWidth;
        Y = Math.floor(index / this.boardWidth);
      }
    }
    const x = this.dropGridX.span(X + 0.5),
          y = this.dropGridY.span(Y + 0.5);
    return [x, y];
  },

  // 
  getPositionOfDrop: function(e) {
    const X = Math.floor((e.pointer.x - this.leftMargin) / this.dropSize),
          Y = Math.floor((e.pointer.y - this.topMargin) / this.dropSize),
          Z = this.boardWidth * Y + X;
    return [X, Y, Z];
  },

  // XYtrue
  // :true, :false)
  isInsideOfBoard(X, Y) {
    if (0 <= X && X < this.boardWidth &&
        0 <= Y && Y < this.boardHeight) {
      return true;
    } else {
      return false;
    }
  },

  // XYtrue
  // :true, :false)
  isOutOfBoard(X, Y) {
    if (X < 0 || this.boardWidth <= X ||
        Y < 0 || this.boardHeight <= Y) {
      return true;
    } else {
      return false;
    }
  },

  // 
  initImmovablePositionSprites: function() {
    this.immovablePositionSprites = new Map();
    for (let index of this.immovablePositions) {
      const sprite = this.createImmovablePositionSprite(index);
      this.immovablePositionSprites.set(index, sprite);
    }
    return this;
  },

  // 
  initStartPositionSprite: function() {
    const index = this.startPosition;
    if (index !== -1) {
      this.startPositionSprite = this.createStartPositionSprite(index);
    }
  },

  // 
  createImmovablePositionSprite(index) {
    const [x, y] = this.getCoordinatesOfDrop(index);
    return ImmovablePosition(this.baseDropSize).moveTo(x, y)
            .setScale(this.dropScale).addChildTo(this.harassmentGroup);
  },

  // 
  createStartPositionSprite(index) {
    const [x, y] = this.getCoordinatesOfDrop(index);
    return StartPosition(this.baseDropSize).moveTo(x, y).rotate()
            .setScale(this.dropScale * 1.3).addChildTo(this.harassmentGroup);
  },
});