import * as phina from "phina.js";
import MyDisplayElement from "../myDisplayElement"
import DragonOutline from "./outline.js";
import DragonBody from "./body.js";
import DragonScale from "./scale.js";
import DragonTail from "./tail.js";
import DragonHead from "./head.js";

phina.globalize();

// ()
// 
// boardWidth(),
// procedure(),
// dropSize()
export default phina.define('Dragon', {
  superClass: MyDisplayElement,
  init: function(boardWidth, procedure, dropSize) {
    this.superInit();

    const lineData = this._getLineData(boardWidth, procedure, dropSize);

    lineData.forEach((data, index) => {
      // 
      DragonOutline(data, dropSize).addChildTo(this);
      // 
      DragonBody(data, dropSize, procedure.length).addChildTo(this);
      // 
      DragonScale(data, dropSize).addChildTo(this);

      if (index === 0) {
        // 
        DragonTail(data, dropSize).addChildTo(this);
      } else if (this._isLast(index, procedure)) {
        // 
        DragonHead(data, dropSize).addChildTo(this);
      }
    });
  },

  // index
  _isLast: function(index, procedure) {
    return (index === (procedure.length - 1));
  },

  // 
  _getLineData: function(boardWidth, procedure, dropSize) {
    return procedure.map((Z, index) => {
      const X = Z % boardWidth,
            Y = Math.floor(Z / boardWidth),
            x = dropSize * (X + 0.5),
            y = dropSize * (Y + 0.5),
            lineType = this._getLineType(boardWidth, procedure, index),
            [beginX, beginY, endX, endY] = this._getCoordinates(lineType, dropSize);

      return {
        index   : index,    // 
        lineType: lineType, // 
        beginX  : beginX,   // dropSize x dropSize x
        beginY  : beginY,   // dropSize x dropSize y
        endX    : endX,     // dropSize x dropSize x
        endY    : endY,     // dropSize x dropSize y
        x       : x,        // Groupx
        y       : y,        // Groupy
      };
    });
  },

  // index
  // (020)
  _getLineType: function(boardWidth, procedure, index) {
    const width = boardWidth;

    if (index === 0) {
      // 
      const diff = procedure[index] - procedure[index + 1];
      if (diff === 1)       return 1; // 
      if (diff === -1)      return 2; // 
      if (diff === width)   return 3; // 
      if (diff === -width)  return 4; // 
    } else if (this._isLast(index, procedure)) {
      // 
      const diff = procedure[index] - procedure[index - 1];
      if (diff === 1)       return 5;  // 
      if (diff === -1)      return 6;  // 
      if (diff === width)   return 7;  // 
      if (diff === -width)  return 8;  // 
    } else {
      // 
      const diff1 = procedure[index] - procedure[index - 1],
            diff2 = procedure[index] - procedure[index + 1];
      if (diff1 === 1 && diff2 === -1)                return 9;   // 
      else if (diff1 === -1     && diff2 === 1)       return 10;  // 
      else if (diff1 === width  && diff2 === -width)  return 11;  // 
      else if (diff1 === -width && diff2 === width)   return 12;  // 
      else if (diff1 === 1      && diff2 === -width)  return 13;  // 
      else if (diff1 === -width && diff2 === 1)       return 14;  // 
      else if (diff1 === width  && diff2 === 1)       return 15;  // 
      else if (diff1 === 1      && diff2 === width)   return 16;  // 
      else if (diff1 === -1     && diff2 === width)   return 17;  // 
      else if (diff1 === width  && diff2 === -1)      return 18;  // 
      else if (diff1 === -width && diff2 === -1)      return 19;  // 
      else if (diff1 === -1     && diff2 === -width)  return 20;  // 
    }
    return 0; // TODO: 0
  },

  // (x, y)(x, y)
  // 
  _getCoordinates: function(lineType, dropSize) {
    const half = dropSize / 2;

    switch (lineType) {
      case  1: return [0, 0, -half, 0];
      case  2: return [0, 0, half, 0];
      case  3: return [0, 0, 0, -half];
      case  4: return [0, 0, 0, half];
      case  5: return [-half, 0, 0, 0];
      case  6: return [half, 0, 0, 0];
      case  7: return [0, -half, 0, 0];
      case  8: return [0, half, 0, 0];
      case  9: return [-half, 0, half, 0];
      case 10: return [half, 0, -half, 0];
      case 11: return [0, -half, 0, half];
      case 12: return [0, half, 0, -half];
      case 13: return [-half, 0, 0, half];
      case 14: return [0, half, -half, 0];
      case 15: return [0, -half, -half, 0];
      case 16: return [-half, 0, 0, -half];
      case 17: return [half, 0, 0, -half];
      case 18: return [0, -half, half, 0];
      case 19: return [0, half, half, 0];
      case 20: return [half, 0, 0, half];
      default: return [0, 0, 0, 0];
    }
  },
});