import * as phina from "phina.js";

phina.globalize();

// 
// 
// cdn(),
// dropSize()
export default phina.define('DragonScale', {
  superClass: 'phina.display.Shape',
  init: function(cdn, dropSize) {
    this.superInit({
      width: dropSize,
      height: dropSize,
      padding: 1,
      backgroundColor: 'transparent',
    });
    this.moveTo(cdn.x, cdn.y)._rotate(cdn, dropSize);
  },
  postrender: function() {
    this._drawScale();
  },
  // V
  _drawScale: function() {
    const canvas = this.canvas,
          offsetX = 13,
          offsetY = 13,
          middleY = 3;

    canvas.lineCap = "butt";
    canvas.lineJoin = "miter";
    canvas.lineWidth = 1.5;
    canvas.strokeStyle = "#000";
    canvas.moveTo(0, offsetY).quadraticCurveTo(0, middleY, -offsetX, -offsetY).stroke();
    canvas.moveTo(0, offsetY).quadraticCurveTo(0, middleY,  offsetX, -offsetY).stroke();
  },
  // 
  _rotate: function(cdn, dropSize) {
    const offset = dropSize * 0.135;

    switch (cdn.lineType) {
    case 2: case 5: case 9:   // 
      this.setRotation(90);
      break;
    case 1: case 6: case 10:  // 
      this.setRotation(-90);
      break;
    case 4: case 7: case 11:  // 
      this.setRotation(180);
      break;
    case 13:  // 
      this.moveBy(-offset, offset);
      this.setRotation(135);
      break;
    case 14:  // 
      this.moveBy(-offset, offset);
      this.scaleX = -1;
      this.setRotation(-45);
      break;
    case 15:  // 
      this.moveBy(-offset, -offset);
      this.setRotation(225);
      break;
    case 16:  // 
      this.moveBy(-offset, -offset);
      this.setRotation(45);
      break;
    case 17:  // 
      this.moveBy(offset, -offset);
      this.setRotation(-45);
      break;
    case 18:  // 
      this.moveBy(offset, -offset);
      this.setRotation(135);
      break;
    case 19:  // 
      this.moveBy(offset, offset);
      this.setRotation(45);
      break;
    case 20:  // 
      this.moveBy(offset, offset);
      this.setRotation(225);
      break;
    }
  },
});