import * as phina from "phina.js";

phina.globalize();

// 
// 
// lineData(),
// dropSize()
export default phina.define('DragonTail', {
  superClass: 'Sprite',
  init: function(lineData, dropSize) {
    const IMG_SIZE = 600,
          scale = (dropSize / IMG_SIZE) * 1.3;

    this.superInit('dragon_tail', IMG_SIZE, IMG_SIZE);
    this.setScale(scale)._move(lineData);
  },
  // 
  _move: function({ x, y, lineType }) {
    this.moveTo(x, y);

    switch (lineType) {
      case 1: this.setRotation(-90); break; //
      case 2: this.setRotation(90);  break; //
      case 4: this.setRotation(180); break; //
    }
    return this;
  },
});