import * as phina from "phina.js";

phina.globalize();

// 
// 
// lineData(),
// dropSize()
export default phina.define('DragonHead', {
  superClass: 'Sprite',
  init: function(lineData, dropSize) {
    const IMG_SIZE = 1000,
          scale = (dropSize / IMG_SIZE) * 1.5;

    this.superInit('dragon_head', IMG_SIZE, IMG_SIZE);
    this.setScale(scale)._move(lineData);
  },
  // 
  _move: function({ x, y, lineType }) {
    this.moveTo(x, y);

    switch (lineType) {
      case 6: this.scaleX *= -1; break;     //
      case 8: this.setRotation(-90); break; //
    }
    return this;
  },
});